import React, {Component} from "react";
import {Avatar, Card, Col, Empty, Icon, Row, Tag} from 'antd';
import IncidentService from "../services/IncidentService";
import ReactPlayer from "react-player";
import { Modal, Button } from 'antd';
import Moment from 'react-moment';
import { Collapse } from 'antd';

const { Panel } = Collapse;
const { Meta } = Card;
const ButtonGroup = Button.Group;

export default class IncidentListView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            videoVisible: false,
            layout: "table",
            incidents: {},
        };
        this.handleDone = this.handleDone.bind(this);
        this.archiveIncident = this.archiveIncident.bind(this);
        this.refresh = this.refresh.bind(this);
    }

    componentDidMount() {
        this.refresh();
    }

    refresh() {
        IncidentService.getIncidents().then(request => {
            this.setState({"incidents": request.data, loading: false})
        })
    }

    archiveIncident(incident) {
        IncidentService.archiveIncident(incident).then(() => {
            this.refresh()
        })
    }

    changeLayout(layout) {
        this.setState({"layout" : layout});
    }

    showVideo(videoid) {

        let cp = ReactPlayer.canPlay("/api/incident/video/"+ this.state.videoid + "/video.mp4");
        this.setState({
            videoVisible: true,
            videoid : videoid
        });
    };

    handleDone() {
        this.setState({
            videoVisible: false,
            videoid : ""
        });
    };

    render() {

        if (this.state.loading || !this.state.incidents) {
            return <Empty description={false}/>
        }

        let layout = this.state.layout;

        return (

            <Collapse bordered={false} defaultActiveKey={['1']}>
                <Panel header="Filter" key="1">
                    <ButtonGroup>
                        <Button type={layout === "list" ? "primary" : ""} size="small" icon="unordered-list" onClick={()=>{this.changeLayout("list")}}/>
                        <Button type={layout === "table" ? "primary" : ""} size="small" icon="table" onClick={()=>{this.changeLayout("table")}}/>
                    </ButtonGroup>
                </Panel>
                <Panel header="Incidents" key="2">
                    {layout === "table" ? (this.renderGrid()) : (this.renderList())}
                </Panel>
            </Collapse>

        )
    }

    renderGrid() {
        let incidents = this.state.incidents;
        return <div style={{background: '#ECECEC', padding: '30px'}}>
            <Modal
                title="Video"
                visible={this.state.videoVisible}
                onOk={this.handleDone}
                onCancel={this.handleDone}
            >
                { this.state.videoVisible ? (<ReactPlayer controls={true} url={"http://localhost:9000/api/incident/video/"+ this.state.videoid + "/video.mp4"}  width="300" />) : (<div><Empty/></div>)}
            </Modal>


            <Row gutter={16}>
                {
                    incidents.map((incident, index) =>
                        <Col span={8} key={index} >
                            <Card
                                title={
                                    <div>
                                        <Tag color="#f50">{incident.event_type}</Tag>
                                        <Tag color="#2db7f5">{incident.event_date}</Tag>
                                        <Tag color="#87d068"><span><Moment format="LTS">{incident.event_start}</Moment></span><Icon type="right"/><span><Moment format="LTS">{incident.event_end}</Moment></span></Tag>
                                        <Tag color="#108ee9">{incident.event_duration}s</Tag>
                                    </div>
                                }
                                bordered={true}
                                cover={<img alt="incident image" src={"/api/incident/image/"+ incident.image_id}  />}
                                actions={[
                                    <Icon type="right" key="play" onClick={() => this.showVideo(incident.video_id)}/>,
                                    <Icon type="edit" key="edit"/>,
                                    <Icon type="delete" key="delete" onClick={() => this.archiveIncident(incident)}/>,
                                ]}
                            >

                            </Card>
                        </Col>
                    )
                }
            </Row>
        </div>
    }

    renderList() {
        let incidents = this.state.incidents;
        return <div>list</div>
    }
}
