import React, {Component} from 'react';
import {Icon, Layout, Menu, } from "antd";
import {NavLink} from "react-router-dom";

const { Sider } = Layout;
const { SubMenu } = Menu;

export default class Sidebar extends Component {
    render() {
        return (
            <Sider
                breakpoint="lg"
                collapsedWidth="0"
                onBreakpoint={broken => {
                    console.log(broken);
                }}
                onCollapse={(collapsed, type) => {
                    console.log(collapsed, type);
                }}
            >
                <div className="logo"/>
                <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
                    <Menu.Item key="1">
                        <Icon type="alert"/>
                        <span className="nav-text"><NavLink to="/incidents"  className="nav-text">Incidents</NavLink></span>
                    </Menu.Item>
                    <Menu.Item key="2">
                        <Icon type="video-camera"/>
                        <span className="nav-text"><NavLink to="/feed"  className="nav-text">Feed</NavLink></span>
                    </Menu.Item>
                    <SubMenu key="admin" title={<span><Icon type="deployment-unit"/>Admin</span>}>
                        <Menu.Item key="3">
                            <Icon type="user"/>
                            <span className="nav-text"><NavLink to="/user"  className="nav-text">Users</NavLink></span>
                        </Menu.Item>
                        <Menu.Item key="4">
                            <Icon type="laptop"/>
                            <span className="nav-text"><NavLink to="/device"  className="nav-text">Device</NavLink></span>
                        </Menu.Item>
                    </SubMenu>
                </Menu>
            </Sider>
        );
    }
}